/**
 * nodeid
 * @param node
 * @param force id
 */
export function setNodeId(node, force) {
    loopNode(node, node => {
        if (force) {
            node.id = uuid();
        } else {
            if (!node.id) node.id = uuid();
        }
    });
}

/**
 *  nodeid
 * @param node
 */
export function deleteNodeId(node) {
    loopNode(node, node => {
        Reflect.deleteProperty(node, 'id');
    });
}
