import {findParentNodeByName} from 'src/drag-page/util/node-util';

export default {
    isContainer: true,
    dropInTo: options => {
        const {targetNode, pageConfig} = options;
        if (targetNode?.componentName === 'Menu') return true;

        let menuNode = findParentNodeByName(pageConfig, 'Menu', targetNode.id);

        return !!menuNode;
    },
    fields: [
        {label: '', field: 'disabled', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', field: 'icon', type: 'ReactNode', version: '4.2.0', desc: ''},
        {label: 'key', field: 'key', type: 'string', version: '', desc: ''},
        {label: '', field: 'title', type: 'ReactNode', version: '', desc: ''},
    ],
};
