import { getFieldUUID } from 'src/drag-page/util';

export default {
    editableContents: [
        {
            selector: '.ant-modal-title',
            propsField: 'title',
        },
        {
            selector: '.ant-modal-footer:not(.customer-modal-footer) .ant-btn-primary',
            propsField: 'okText',
        },
        {
            selector: '.ant-modal-footer:not(.customer-modal-footer) .ant-btn:not(.ant-btn-primary)',
            propsField: 'cancelText',
        },
    ],

    // draggable: false,
    componentDisplayName: ({ node }) => {
        const { componentName, props = {} } = node;
        const { title } = props;

        if (!title) return componentName;

        return `${componentName} ${title}`;
    },

    propsToSet: {
        onClick: 'func.handleShowModal',
    },
    hooks: {
        beforeAdd: options => {
            const { node, dragPageState: { pageConfig } } = options;
            if (!node.props) node.props = {};
            const uid = getFieldUUID();
            const field = `visible__${uid}`;
            const handleModalCancel = `handleModalCancel__${uid}`;
            const handleModalShow = `handleModalShow__${uid}`;
            node.__config = {
                pageState: {
                    [field]: false,
                },
                pageFunction: {
                    [handleModalShow]: `() => {setState({${field}: true})}`,
                    [handleModalCancel]: `() => {setState({${field}: false})}`,
                },
            };

            node.props.visible = `state.${field}`;
            node.props.onCancel = `func.${handleModalCancel}`;
            node.propsToSet = {
                onClick: `func.${handleModalShow}`,
            };

            if (!pageConfig.children) pageConfig.children = [];
            pageConfig.children.push(node);
        },
    },
    fields: [
        // {label: '', field: 'visible', type: 'boolean', version: '', desc: ''},
        { label: '', field: 'title', type: 'string', version: '', desc: '' },
        { label: '', field: 'width', type: 'unit', defaultValue: 520, version: '', desc: '' },
        { label: '', category: '', field: 'mask', type: 'boolean', defaultValue: true, version: '', desc: '' },
        { label: '', category: '', field: 'maskClosable', type: 'boolean', defaultValue: true, version: '', desc: '' },
        { label: 'esc', category: '', field: 'keyboard', type: 'boolean', defaultValue: true, version: '', desc: ' esc ' },
        { label: '', category: '', field: 'centered', type: 'boolean', defaultValue: false, version: '', desc: ' Modal' },
        { label: '', field: 'footer', type: 'FooterSwitch', version: '', desc: ' footer={null}' },
        { label: '', field: 'okText', appendField: { footer: undefined }, type: 'string', defaultValue: '', version: '', desc: '' },
        // {label: '', field: 'okType', appendField: {footer: undefined}, type: 'radio-group', options: buttonTypeOptions, defaultValue: 'primary', version: '', desc: ''},
        { label: 'loading', field: 'confirmLoading', appendField: { footer: undefined }, type: 'boolean', defaultValue: false, version: '', desc: ' loading' },
        { label: '', field: 'cancelText', appendField: { footer: undefined }, type: 'string', defaultValue: '', version: '', desc: '' },
        { label: 'z-index', field: 'zIndex', type: 'number', defaultValue: 1000, version: '', desc: ' Modal  z-index' },
        /*
        {label: 'Modal ', field: 'afterClose', type: 'function', version: '', desc: 'Modal '},
        {label: 'Modal body ', field: 'bodyStyle', type: 'CSSProperties', defaultValue: '', version: '', desc: 'Modal body '},
        {label: 'cancel  props', field: 'cancelButtonProps', type: 'ButtonProps', version: '', desc: 'cancel  props'},
        {label: '', field: 'closable', type: 'boolean', defaultValue: true, version: '', desc: ''},
        {label: '', field: 'closeIcon', type: 'ReactNode', defaultValue: '<CloseOutlined />', version: '', desc: ''},
        {label: ' Modal ', field: 'destroyOnClose', type: 'boolean', defaultValue: false, version: '', desc: ' Modal '},
        {label: '', field: 'focusTriggerAfterClose', type: 'boolean', defaultValue: true, version: '4.9.0', desc: ''},

        {label: ' Modal', field: 'forceRender', type: 'boolean', defaultValue: false, version: '', desc: ' Modal'},
        {
            label: ' Modal  HTML , false  dom',
            field: 'getContainer',
            type: 'enum',
            defaultValue: 'document.body',
            version: '',
            options: [{value: 'HTMLElement', label: 'HTMLElement'}, {value: '() => HTMLElement', label: '() => HTMLElement'}, {value: 'Selectors', label: 'Selectors'}, {value: 'false', label: 'false'}],
            desc: ' Modal  HTML , false  dom',
        },


        {label: '', field: 'maskStyle', type: 'CSSProperties', defaultValue: '', version: '', desc: ''},
        {label: '', field: 'modalRender', type: '(node: ReactNode) => ReactNode', version: '4.7.0', desc: ''},
        {label: 'ok  props', field: 'okButtonProps', type: 'ButtonProps', version: '', desc: 'ok  props'},
        {label: '', field: 'style', type: 'CSSProperties', version: '', desc: ''},
        {label: '', field: 'wrapClassName', type: 'string', version: '', desc: ''},
        {label: '', field: 'onCancel', type: 'function(e)', version: '', desc: ''},
        {label: '', field: 'onOk', type: 'function(e)', version: '', desc: ''},
        */
    ],
};
