import React, {useEffect, useState, useCallback} from 'react';
import {
    Form,
    InputNumber,
} from 'antd';
import {Icon} from 'src/components';
import {RadioGroup, UnitInput, RectInputsWrapper} from 'src/drag-page/components';
import {handleSyncFields} from '../util';
import s from './style.less';

const displayOptions = [
    {value: 'inline', label: '', icon: <Icon type="icon-display-inline"/>},
    {value: 'flex', label: '', icon: <Icon type="icon-display-flex"/>},
    {value: 'inline-flex', label: '', icon: <Icon type="icon-display-inline-flex"/>},
    {value: 'block', label: '', icon: <Icon type="icon-display-block"/>},
    {value: 'inline-block', label: '', icon: <Icon type="icon-display-inline-block"/>},
    {value: 'none', label: '', icon: <Icon type="icon-display-none"/>},
];

const flexDirectionOptions = [
    {value: 'row', label: '', tip: ''},
    {value: 'row-reverse', label: '', tip: ''},
    {value: 'column', label: '', tip: ''},
    {value: 'column-reverse', label: '', tip: ''},
];

const justifyContentOptions = [
    {value: 'flex-start', label: '', icon: <Icon type="icon-content-start"/>},
    {value: 'flex-end', label: '', icon: <Icon type="icon-content-end"/>},
    {value: 'center', label: '', icon: <Icon type="icon-content-center"/>},
    {value: 'space-between', label: '', icon: <Icon type="icon-content-between"/>},
    {value: 'space-around', label: '', icon: <Icon type="icon-content-around"/>},
];
const alignItemsOptions = [
    {value: 'flex-start', label: '', icon: <Icon type="icon-items-start"/>},
    {value: 'flex-end', label: '', icon: <Icon type="icon-items-end"/>},
    {value: 'center', label: '', icon: <Icon type="icon-items-center"/>},
    {value: 'baseline', label: '', icon: <Icon type="icon-items-baseline"/>},
    {value: 'stretch', label: '', icon: <Icon type="icon-items-stretch"/>},
];
const flexWrapOptions = [
    {value: 'nowrap', label: ''},
    {value: 'wrap', label: ''},
    {value: 'wrap-reserve', label: ''},
];
const marginFields = [
    'marginTop',
    'marginRight',
    'marginBottom',
    'marginLeft',
];
const paddingFields = [
    'paddingTop',
    'paddingRight',
    'paddingBottom',
    'paddingLeft',
];

const Layout = React.memo(function Layout(props) {
    const {canvasDocument, componentId, form} = props;
    const [parentIsFlexBox, setParentIsFlexBox] = useState(false);

    const handleChange = useCallback(() => form.submit(), [form]);

    // flex 
    useEffect(() => {
        if (!canvasDocument || !componentId) return;

        const ele = canvasDocument.querySelector(`.id_${componentId}`);
        if (!ele?.parentNode) return;

        const display = window.getComputedStyle(ele.parentNode).display;
        setParentIsFlexBox(display === 'flex' || display === 'inline-flex');
    }, [componentId, canvasDocument]);

    return (
        <div className={s.root}>
            <Form.Item
                label=""
                name="display"
                colon={false}
            >
                <RadioGroup options={displayOptions}/>
            </Form.Item>
            <Form.Item shouldUpdate noStyle>
                {({getFieldValue}) => {
                    const display = getFieldValue('display');
                    if (display !== 'flex' && display !== 'inline-flex') return null;

                    return (
                        <>
                            <Form.Item
                                label=""
                                name="flexDirection"
                                colon={false}
                            >
                                <RadioGroup options={flexDirectionOptions}/>
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="justifyContent"
                                colon={false}
                            >
                                <RadioGroup options={justifyContentOptions}/>
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="alignItems"
                                colon={false}
                            >
                                <RadioGroup options={alignItemsOptions}/>
                            </Form.Item>
                            <Form.Item
                                label=""
                                name="flexWrap"
                                colon={false}
                            >
                                <RadioGroup options={flexWrapOptions}/>
                            </Form.Item>
                        </>
                    );
                }}
            </Form.Item>
            <RectInputsWrapper tip="margin" style={{height: 180, marginBottom: 8}}>
                {marginFields.map(item => (
                    <Form.Item
                        key={item}
                        name={item}
                        noStyle
                        colon={false}
                    >
                        <UnitInput
                            allowClear={false}
                            placeholder="0"
                            style={{userSelect: 'none'}}
                            onClick={event => handleSyncFields({event, form, fields: marginFields, field: item, onChange: handleChange})}
                            onKeyDown={event => handleSyncFields({enter: true, event, form, fields: marginFields, field: item, onChange: handleChange})}
                        />
                    </Form.Item>
                ))}
                <RectInputsWrapper tip="padding" inner>
                    {paddingFields.map(item => (
                        <Form.Item
                            key={item}
                            name={item}
                            noStyle
                            colon={false}
                        >
                            <UnitInput
                                allowClear={false}
                                placeholder="0"
                                style={{userSelect: 'none'}}
                                onClick={event => handleSyncFields({event, form, fields: paddingFields, field: item, onChange: handleChange})}
                                onKeyDown={event => handleSyncFields({enter: true, event, form, fields: paddingFields, field: item, onChange: handleChange})}
                            />
                        </Form.Item>
                    ))}
                    <div className={s.innerInput}>
                        <Form.Item
                            label=""
                            name="width"
                            colon={false}
                        >
                            <UnitInput
                                style={{width: 60, marginRight: 8}}
                                allowClear={false}
                                placeholder="width"
                            />
                        </Form.Item>
                        <Form.Item
                            label=""
                            name="height"
                            colon={false}
                        >
                            <UnitInput
                                style={{width: 60}}
                                allowClear={false}
                                placeholder="height"
                            />
                        </Form.Item>
                    </div>
                </RectInputsWrapper>
            </RectInputsWrapper>
            {parentIsFlexBox ? (
                <>
                    <Form.Item
                        label=""
                        name="flexGrow"
                        colon={false}
                    >
                        <InputNumber style={{width: '100%'}} placeholder="flex-grow" min={0} step={1}/>
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="flexShrink"
                        colon={false}
                    >
                        <InputNumber style={{width: '100%'}} placeholder="flex-shrink" min={0} step={1}/>
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="flexBasis"
                        colon={false}
                    >
                        <UnitInput placeholder="flex-basis"/>
                    </Form.Item>
                </>
            ) : null}
        </div>
    );
});


// style  form
Layout.styleToFormValues = (style) => style;

// formstyle
Layout.formValuesToStyle = (changedValues, allValues) => {
    const {display} = allValues;

    // flex
    if (display !== 'flex' && display !== 'inline-flex') {
        allValues.flexDirection = undefined;
        allValues.justifyContent = undefined;
        allValues.alignItems = undefined;
        allValues.flexWrap = undefined;
    }

    return allValues;
};

export default Layout;
