import React, {createElement} from 'react';
import {getComponent, useNodeChange} from 'src/drag-page/util';
import {isNode} from 'src/drag-page/util/node-util';
import {getComponentConfig} from 'src/drag-page/component-config';
import {store, actions} from 'src/models';
import {cloneDeep} from 'lodash';
import s from './style.less';

//  
const loop = (obj, cb) => {
    if (typeof obj !== 'object' || obj === null) return;

    // entries 
    Object.entries(obj)
        .forEach(([key, value]) => {
            if (
                typeof value !== 'object'
                || isNode(value)
                || React.isValidElement(value)
            ) {
                return cb(obj, key, value);
            }

            loop(value, cb);
        });
};

const NodeRender = React.memo(function(renderNodeProps) {
    // console.log('NodeRender');
    let {
        config,
        isPreview = true,
        pageRenderRoot,
        className,
        state,
        func,
        variable,
        ...others
    } = renderNodeProps;

    const dragPageAction = actions.dragPage;

    useNodeChange(config);

    // 
    if (!isNode(config)) return config;

    // DragHolder
    if (isPreview && config.componentName === 'DragHolder') return null;

    let {
        id,
        key,
        wrapper,
        componentName,
        children,
    } = config;

    const componentConfig = getComponentConfig(componentName);

    let {
        draggable,
        childrenDraggable,
        render,
        hooks: {
            beforeRender,
            afterRender,
            renderChildren,
        } = {},
    } = componentConfig;

    if (render === false) return;

    const renderProps = {
        isPreview,
        pageRenderRoot,
        state,
        func,
        variable,
    };

    // hooks 
    const hooksArgs = {
        node: config,
        NodeRender,
        renderProps,
        dragPageState: store.getState().dragPage,
        dragPageAction: actions.dragPage,
    };

    const isRender = beforeRender && beforeRender(hooksArgs);

    if (isRender === false) return null;

    setTimeout(() => {
        afterRender && afterRender(hooksArgs);

        const dragProps = {draggable};

        // draggabledom
        const ele = pageRenderRoot?.querySelector(`.id_${id}`);
        if (!ele) return;

        if (isPreview) {
            ele.classList.remove(s.draggable);
            Object.entries(dragProps).forEach(([key, value]) => {
                ele.removeAttribute(key);
            });
        } else {
            ele.classList.add(s.draggable);
            Object.entries(dragProps).forEach(([key, value]) => {
                ele.setAttribute(key, value);
            });
        }
    });

    // props setState 
    const props = cloneDeep(config.props || {});
    // eslint-disable-next-line no-unused-vars
    const setState = dragPageAction.setPageState;

    //  wrapperwrapper
    if (wrapper?.length) {
        wrapper[0].children = [{...config, wrapper: null}];

        const nextConfig = wrapper.reduce((prev, curr) => {
            curr.children = [prev];

            return {...curr};
        });

        return (
            <NodeRender
                key={nextConfig.id}
                {...renderProps}
                config={{...nextConfig}}
            />
        );
    }

    // props   
    loop(props, (obj, key, value) => {
        // 
        if (isNode(value)) {
            obj[key] = (
                <NodeRender
                    key={value.id}
                    {...renderProps}
                    config={value}
                />
            );
        }
        if (typeof value === 'string') {
            // props  pageState
            if (value.startsWith('state.')) {
                // eslint-disable-next-line no-eval
                obj[key] = eval(value);
            }
            // props  pageFunction
            if (value.startsWith('func.')) {
                // eslint-disable-next-line no-eval
                obj[key] = eval(eval(value));
            }
            // props  pageVariable
            if (value.startsWith('variable.')) {
                // eslint-disable-next-line no-eval
                obj[key] = eval(value);
            }
        }
    });

    const component = getComponent(config).component;

    let childrenEle = (() => {
        if (renderChildren) return renderChildren(hooksArgs);

        if (children?.length > 1) {
            return children.map(childConfig => (
                <NodeRender
                    key={childConfig.id}
                    {...renderProps}
                    isPreview={isPreview || childrenDraggable === false}
                    config={childConfig}
                />
            ));
        }
        if (children?.length === 1) {
            const childConfig = children[0];
            return (
                <NodeRender
                    key={childConfig.id}
                    {...renderProps}
                    isPreview={isPreview || childrenDraggable === false}
                    config={childConfig}
                />
            );
        }

        return null;
    })();

    // id
    const cls = [props.className, `id_${id}`, className].filter(item => !!item).join(' ');

    // console.log(config.componentName, props, others);
    return createElement(component, {
        key: key || id, // keykeyidkey
        ...props,
        ...others,
        className: cls,
        children: childrenEle,
    });
});

// antd
NodeRender.__ANT_BREADCRUMB_SEPARATOR = true;
NodeRender.__ANT_BREADCRUMB_ITEM = true;

export default NodeRender;
