import {targetOptions, buttonTypeOptions} from '../options';

export default {
    isContainer: false,
    childrenDraggable: false,
    renderAsDisplayName: true,
    componentDisplayName: options => {
        const {node} = options;

        const textNode = node?.children?.find(item => item.componentName === 'Text');

        const text = textNode?.props?.text;

        if (text) return `Button ${text}`;

        return 'Button';
    },
    fields: [
        {
            label: '', field: 'type', type: 'radio-group', defaultValue: 'default', version: '',
            options: buttonTypeOptions,
            desc: '',
        },
        {label: '', category: '', categoryOrder: 1, field: 'danger', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', category: '', field: 'disabled', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', category: '', field: 'loading', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {
            label: '', field: 'shape', type: 'radio-group', version: '',
            options: [
                {value: 'circle', label: ''},
                {value: 'round', label: ''},
            ],
            desc: '',
        },
        {
            label: '', field: 'size', type: 'radio-group', defaultValue: 'middle', version: '',
            options: [
                {value: 'large', label: ''},
                {value: 'small', label: ''},
            ],
            desc: '',
        },
        {
            label: '',
            field: 'icon',
            type: 'ReactNode',
            version: '',
            desc: '',
        },
        {label: '', category: '', field: 'ghost', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', category: '', field: 'block', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', field: 'href', type: 'string', version: '', desc: ' button  a '},
        {
            label: '', appendField: 'href', field: 'target', type: 'radio-group', version: '', desc: ' a  target href ',
            options: targetOptions,
        },
        {label: 'htmlType', field: 'htmlType', type: 'string', defaultValue: 'button', version: '', desc: ' button  type  HTML '},
        {label: '', field: 'onClick', type: 'function', version: '', desc: ''},
    ],
};
