import React from 'react';
import {getFieldOption, getFieldUUID} from 'src/drag-page/util';

export default {
    componentType: '@ra-lib/admin',
    dropAccept: 'Table.Column',
    hooks: {
        beforeRender: options => {
            setTableColumns(options);
        },
        beforeSchemaEdit: options => {
            const {node} = options;
            if (node?.props) {
                Reflect.deleteProperty(node.props, 'columns');
            }
        },
        beforeToCode: ({node, pageState, pageStateDefault, pageFunction}) => {
            const {columns = [], rowSelection} = node.props || {};
            const {children = []} = node;

            const uid = getFieldUUID();

            const dataSourceField = `dataSource__${uid}`;
            pageState[dataSourceField] = [];
            pageStateDefault[dataSourceField] = [];

            node.props.dataSource = `state.${dataSourceField}`;
            columns.forEach((col, index) => {
                // 
                const tableColumn = children[index];
                Object.entries((tableColumn.props || {}))
                    .forEach(([key, value]) => {
                        const fieldOption = getFieldOption(tableColumn, key) || {};
                        const {defaultValue} = fieldOption;

                        // 
                        if (JSON.stringify(defaultValue) === JSON.stringify(value)) {
                            Reflect.deleteProperty(col, key);
                        }
                    });

                // render
                const {props: {render}} = tableColumn;
                if (render) col.render = render;

                //  className id_{uuid}
                Reflect.deleteProperty(col, 'className');
            });

            if (rowSelection === true) {
                const selectedRowKeysField = `selectedRowKeys__${uid}`;
                const handleSelectedRowKeysChangeField = `handleSelectedRowKeysChange__${uid}`;

                pageState[selectedRowKeysField] = [];
                pageStateDefault[selectedRowKeysField] = [];

                pageFunction[handleSelectedRowKeysChangeField] = `selectedRowKeys => {setState({${selectedRowKeysField}: selectedRowKeys})}`;

                node.props.rowSelection = {
                    selectedRowKeys: `state.${selectedRowKeysField}`,
                    onChange: `func.${handleSelectedRowKeysChangeField}`,
                };
            }
        },
    },
    fields: [
        {label: '', category: '', field: 'bordered', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', category: '', field: 'loading', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', category: '', field: 'pagination', type: 'boolean', version: '', desc: ' pagination  false '},
        {label: '', category: '', field: 'rowSelection', type: 'boolean', version: '', desc: ''},
        {label: '', category: '', field: 'showHeader', type: 'boolean', defaultValue: true, version: '', desc: ''},
        {
            label: '', field: 'size', type: 'radio-group', defaultValue: 'default', version: '',
            options: [
                {value: 'default', label: ''},
                {value: 'middle', label: ''},
                {value: 'small', label: ''},
            ],
            desc: '',
        },
        {label: '', field: 'fitHeight', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', field: '__columns', type: 'ColumnFast'},
        {
            label: '', field: 'scroll',
            type: {
                value: 'object',
                fields: [
                    {label: '', field: 'scrollToFirstRowOnChange', type: 'boolean', defaultValue: false},
                    {label: '', field: 'y', type: 'unit'},
                    {label: '', field: 'x', type: 'unit'},
                ],
            },
            version: '', desc: '',
        },
        {
            label: '', field: 'expandable',
            type: {
                value: 'object',
                fields: [
                    {label: '', field: 'columnWidth', type: 'unit', desc: ''},
                    {label: '', field: 'expandedRowRender', type: 'ReactNode', functionType: true, desc: ''},
                    {label: '', field: 'expandRowByClick', type: 'boolean', desc: ''},
                    {label: '', field: 'indentSize', type: 'number', defaultValue: 15, desc: ' px '},
                ],
            },
            version: '', desc: '',
        },
    ],
};


function setTableColumns({node: tableNode, NodeRender, renderProps}) {
    if (!tableNode) return;

    let {children, props} = tableNode;
    if (!props.columns) props.columns = [];

    if (!children?.length) {
        props.columns = [];
        return;
    }

    const loop = (node, columns) => {
        const {id, props = {}, children} = node;
        const {render, ...otherProps} = props;
        const col = {...otherProps, className: `id_${id}`};
        if (render) col.render = () => <NodeRender {...renderProps} config={render}/>;

        columns.push(col);
        if (children?.length) {
            col.children = [];
            children.forEach(item => loop(item, col.children));
        }
    };
    const columns = [];
    children.forEach(node => loop(node, columns));

    props.columns = columns;
    if (!tableNode.props) tableNode.props = {};
    tableNode.props.columns = columns;
}


