export default {
    componentDisplayName: options => {
        const {node} = options;

        const textNode = node?.children?.find(item => item.componentName === 'Text');

        const text = textNode?.props?.text;

        if (text) return `Divider ${text}`;

        return 'Divider';
    },
    fields: [
        {label: '', category: '', field: 'dashed', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', category: '', field: 'plain', type: 'boolean', defaultValue: false, version: '4.2.0', desc: ''},
        {
            label: '', field: 'type', type: 'radio-group', defaultValue: 'horizontal', version: '',
            options: [
                {value: 'horizontal', label: ''},
                {value: 'vertical', label: ''},
            ],
            desc: '',
        },
        {
            label: '', field: 'orientation', type: 'radio-group', defaultValue: 'center', version: '',
            options: [
                {value: 'left', label: ''},
                {value: 'right', label: ''},
                {value: 'center', label: ''},
            ],
            desc: '',
        },
    ],
};

