import config from 'src/commons/config-hoc';
import {useMemo} from 'react';
import {GUIDE_COLORS} from 'src/drag-page/component-canvas/iframe-render/drag-guide';

export default config({
    connect: state => {
        return {
            draggingNode: state.dragPage.draggingNode,
            targetNode: state.dragPage.targetNode,
            targetHoverPosition: state.dragPage.targetHoverPosition,
        };
    },
})(function Tip(props) {
    const {
        draggingNode,
        targetNode,
        targetHoverPosition,
    } = props;

    const message = useMemo(() => {
        if (!draggingNode || !targetNode) return;

        const position = {
            top: '',
            left: '',
            center: '',
            bottom: '',
            right: '',
        }[targetHoverPosition];

        const {componentName: targetName} = targetNode;
        const draggingName = draggingNode?.config?.componentName || '';
        const tip = draggingNode?.tip;
        const {type, dropType, propsToSet = {}} = draggingNode;
        const color = (GUIDE_COLORS[dropType] || GUIDE_COLORS.default)(1);

        if (tip) return <span style={{color}}>{tip({draggingName, targetName, targetNode})}</span>;

        if (dropType === 'props') return <span style={{color}}> {draggingName}  {targetName}  {Object.keys(propsToSet)} </span>;
        if (dropType === 'replace') return <span style={{color}}>{draggingName}  {targetName} </span>;
        if (dropType === 'wrapper') return <span style={{color}}>{draggingName}  {targetName} </span>;

        if (type === 'new') return <span style={{color}}> {targetName} {position}  {draggingName}</span>;
        if (type === 'move') return <span style={{color}}> {draggingName}  {targetName} {position}</span>;

    }, [
        draggingNode,
        targetNode,
        targetHoverPosition,
    ]);

    return (
        <div>
            {message}
        </div>
    );
});
