import {isMac, getFormItemName, getFieldOption} from 'src/drag-page/util';
import {findParentNodeByName} from 'src/drag-page/util/node-util';
import {getComponentConfig} from 'src/drag-page/component-config';
import {colFields, getOnKeyDown} from '../common/Col';
import {formElementTypes} from '@ra-lib/admin';
import {options as ruleOptions} from 'src/drag-page/components/item-rules';

export default {
    editableContents: [
        {
            selector: '.ant-col.ant-form-item-label>label',
            propsField: 'label',
        },
    ],
    withHolder: true,
    holderProps: {
        tip: '',
        style: {height: 'auto'},
    },
    // childrenDraggable: true, // 
    // dropAccept: options => { // 
    //     const {draggingNode} = options;
    //     const nodeConfig = getComponentConfig(draggingNode?.nodeData?.componentName);
    //
    //     return nodeConfig?.isFormElement;
    // },
    // Form
    dropInTo: options => {
        const {targetNode, pageConfig} = options;
        if (targetNode?.componentName === 'Form') return true;

        let formNode = findParentNodeByName(pageConfig, 'Form', targetNode.id);

        return !!formNode;
    },
    hooks: {
        afterAdd: (options) => {
            const {node, dragPageState: {pageConfig}} = options;
            if (node?.props?.name) {
                node.props.name = getFormItemName(node, pageConfig);
            }
        },
        beforeAddChildren: (options) => {
            const {node, childNode} = options;

            if (!node) return;

            //  
            if (
                node.children?.length === 1
                && getComponentConfig(node.children[0].componentName).isFormElement
            ) {
                node.children[0].toDelete = true;
            }

            if (childNode?.componentName === 'Switch') {
                if (!node.props) node.props = {};

                node.props.valuePropName = 'checked';
            }
        },
        afterAddChildren: options => {
            const {node} = options;
            if (!node) return;
            if (!node?.children?.length) return;

            const index = node.children.findIndex(item => item.toDelete);
            if (index !== -1) {
                node.children.splice(index, 1);
            }
        },
        beforeRender: ({node}) => {
            const {props} = node;
            if (props?.rules?.length) {
                props.rules = props.rules.map(item => {
                    const data = ruleOptions.find(it => it.value === item);
                    return data?.rule;
                }).filter(item => !!item);
            }
        },
        beforeToCode: ({node, imports, useRaFormItem}) => {
            // 
            const rules = node?.props?.rules;
            if (rules?.length) {
                node.props.rules = `{[${rules.map(item => {
                    const data = ruleOptions.find(it => it.value === item);
                    if (data) {
                        if (data.ruleStr.includes('validationRule')) {

                            const options = {name: 'validationRule'};
                            const objSets = imports.get('ra-lib');
                            if (!objSets) {
                                const set = new Set();
                                set.add(options);
                                imports.set('@ra-lib/admin', set);
                            } else {
                                objSets.add(options);
                            }
                        }
                        return data.ruleStr;
                    }
                    return undefined;
                }).filter(item => !!item).join(',')}]}`;
            }

            //  ra-lib FormItem
            if (useRaFormItem) {
                node.componentName = 'FormItem';
                const options = {name: 'FormItem'};
                const objSets = imports.get('@ra-lib/admin');
                if (!objSets) {
                    const set = new Set();
                    set.add(options);
                    imports.set('@ra-lib/admin', set);
                } else {
                    objSets.add(options);
                }
            }
            if (useRaFormItem && node?.children?.length === 1) {
                const eleNode = node.children[0];
                const eleNodeConfig = getComponentConfig(eleNode.componentName);
                const isFormElement = eleNodeConfig?.isFormElement;
                if (isFormElement) {
                    if (!node.props) node.props = {};
                    if (!eleNode.props) eleNode.props = {};

                    // 
                    console.log(eleNode.props);
                    Object.entries(eleNode.props).forEach(([key, value]) => {
                        const fieldOption = getFieldOption(eleNode, key) || {};
                        const {defaultValue} = fieldOption;

                        // 
                        if (key === 'key' || key.startsWith('_')) {
                            Reflect.deleteProperty(eleNode.props, key);
                        }

                        // style
                        if (key === 'style' && JSON.stringify(value) === '{"width":"100%"}') {
                            Reflect.deleteProperty(eleNode.props, key);
                        }

                        // 
                        if (JSON.stringify(defaultValue) === JSON.stringify(value)) {
                            Reflect.deleteProperty(eleNode.props, key);
                        }
                    });

                    node.props = {
                        ...node.props,
                        ...eleNode.props,
                        placeholder: undefined,
                    };

                    let type = formElementTypes.find(item => item.componentName === eleNode.componentName)?.type || undefined;
                    if (type === 'input') type = undefined;

                    node.props.type = type;
                    node.children = undefined;
                }
            }
        },
    },
    componentDisplayName: ({node}) => {
        const {componentName, props = {}} = node;
        const {label} = props;

        if (!label) return componentName;

        return `${componentName} ${label}`;
    },

    fields: [
        // {label: '', category: '', field: 'required', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {label: '', category: '', field: 'hasFeedback', type: 'boolean', defaultValue: false, version: '', desc: ' validateStatus  Input '},
        {label: '', category: '', field: 'colon', type: 'boolean', defaultValue: true, version: '', desc: ' label  label '},
        {label: '', category: '', field: 'noStyle', type: 'boolean', defaultValue: false, version: '', desc: ' true '},

        {label: '', field: 'label', type: 'string', version: '', desc: 'label '},
        {label: '', field: 'name', type: 'string', version: '', desc: ''},
        {label: '', field: 'rules', type: 'Rule', version: '', desc: ''},

        {label: '', field: 'tooltip', type: 'string', version: '4.7.0', desc: ''},
        {
            label: '', field: 'labelAlign', type: 'radio-group', defaultValue: 'right', version: '',
            options: [
                {value: 'left', label: ''},
                {value: 'right', label: ''},
            ],
            desc: '',
        },
        {
            label: '', field: 'labelCol',
            type: {
                value: 'object',
                fields: colFields.map(item => {

                    return {
                        ...item,
                        onKeyDown: getOnKeyDown(value => ({labelCol: {[item.field]: value}}), 'Form'),
                    };
                }),
            },
            version: '',
            placeholder: `labelCol  ${isMac ? '' : 'ctrl'}+Enter `,
            title: `${isMac ? '' : 'ctrl'}+Enter `,
        },
        {
            label: '', field: 'wrapperCol',
            type: {
                value: 'object',
                fields: colFields.map(item => {

                    return {
                        ...item,
                        onKeyDown: getOnKeyDown(value => ({wrapperCol: {[item.field]: value}}), 'Form'),
                    };
                }),
            },
            version: '',
            placeholder: `labelCol  ${isMac ? '' : 'ctrl'}+Enter `,
            title: `${isMac ? '' : 'ctrl'}+Enter `,
        },
    ],
};

