import { getFieldUUID } from 'src/drag-page/util';

export default {
    componentType: '@ra-lib/admin',
    isContainer: false,
    hooks: {
        beforeAdd: ({node}) => {
            const uid = getFieldUUID();
            const totalField = `total__${uid}`;
            const pageNumField = `pageNum__${uid}`;
            const pageSizeField = `pageSize__${uid}`;
            const handlePageNumChangeField = `handlePageNumChange__${uid}`;
            const handlePageSizeChangeField = `handlePageSizeChange__${uid}`;

            node.__config = {
                pageState: {
                    [totalField]: 100,
                    [pageNumField]: 1,
                    [pageSizeField]: 20,
                },
                pageFunction: {
                    [handlePageNumChangeField]: `(pageNum) => {setState({${pageNumField}: pageNum})}`,
                    [handlePageSizeChangeField]: `(pageSize) => {setState({${pageNumField}: 1, ${pageSizeField}: pageSize})}`,
                },
            };

            const props = {
                total: `state.${totalField}`,
                pageNum: `state.${pageNumField}`,
                pageSize: `state.${pageSizeField}`,
                onPageNumChange: `func.${handlePageNumChangeField}`,
                onPageSizeChange: `func.${handlePageSizeChangeField}`,
            };
            node.props = {...node.props, ...props};
        },
    },
    fields: [
        // {label: '', field: 'pageSize', type: 'number', version: '', desc: ''},
        {label: '', field: 'showQuickJumper', type: 'boolean', defaultValue: false, version: '', desc: ''},
        {
            label: '', field: 'size', type: 'radio-group', defaultValue: 'default', version: '',
            options: [
                {value: 'default', label: ''},
                {value: 'small', label: ''},
            ],
            desc: ' small ',
        },
        // {label: '', field: 'total', type: 'number', defaultValue: '0', version: '', desc: ''},
        // {label: '', field: 'showTotal', type: 'string', version: '', desc: ''},
    ],
};

