import { v4 as uuid } from 'uuid';
import base from './base';
import navigation from './navigation';
import common from './common';
import form from './form';
import dataInput from './data-input';
import dataShow from './data-show';
import feedback from './feedback';
import other from './other';
import icon from './icon';
import baseModule from './module';
import page from './page';


export default [
    { title: '', children: page },
    { title: '', children: baseModule },
    { title: '', children: base },
    { title: '', children: common },
    { title: '', children: navigation },
    { title: '', children: form },
    { title: '', children: dataInput },
    { title: '', children: dataShow },
    { title: '', children: feedback },
    { title: '', children: other },
    { title: '', children: icon },
].map(item => {
    item.id = uuid();
    item.children = setDefaultConfig(item.children);

    return item;
});

function setDefaultConfig(subCategories) {
    const defaultConfig = {
        renderPreview: true, //  config
        previewProps: {}, // 
    };

    if (!subCategories?.length) return subCategories;

    subCategories.forEach(subCategory => {
        if (!subCategory.id) subCategory.id = uuid();

        (subCategory?.children || []).forEach(node => {
            if (!node.id) node.id = uuid();

            Object.entries(defaultConfig)
                .forEach(([key, value]) => {
                    if (!(key in node)) node[key] = value;
                });

            // TODO 
            // const {config} = node;

        });
    });

    return subCategories;
}

