import {store} from 'src/models';

export default {
    editableContents: [
        {
            selector: options => {
                const {node} = options;
                return `th.id_${node.id}`;
            },
            propsField: 'title',

            // 
            onClick: e => options => {
                const {node, dragPageAction} = options;
                const {nodeSelectType} = store.getState().dragPage;
                if (nodeSelectType === 'meta') {
                    if ((e.metaKey || e.ctrlKey)) {
                        e.stopPropagation();
                        // 
                        dragPageAction.setSelectedNodeId(node.id);
                    }
                }

                // 
                if (nodeSelectType === 'click') {
                    e.stopPropagation();
                    dragPageAction.setSelectedNodeId(node.id);
                }
            },
        },
    ],
    render: false,
    dropAccept: 'Table.Column',
    dropInTo: ['Table', 'Table.Column'],
    componentDisplayName: ({node}) => {
        const title = node.props?.title || '';

        return `Column ${title}`;
    },
    fields: [
        {label: '', field: 'title', type: 'string'},
        {label: '', field: 'dataIndex', type: 'string'},
        {label: '', field: 'width', type: 'unit'},
        {label: '', field: 'render', type: 'ReactNode', functionType: true},
        {
            label: '', field: 'align', type: 'radio-group', defaultValue: 'left', version: '',
            options: [
                {value: 'left', label: ''},
                {value: 'center', label: ''},
                {value: 'right', label: ''},
            ],
            desc: '',
        },
        {
            label: '',
            category: '',
            categoryOrder: 5,
            field: 'ellipsis',
            type: 'boolean',
            defaultValue: false,
            version: 'showTitle: 4.3.0',
            desc: '\\n true  { showTitle?: boolean }  tableLayout=\'fixed\'',
        },
        {label: '', category: '', field: 'sorter', type: 'boolean', defaultValue: false},
        {
            label: '', appendField: 'sorter', field: 'defaultSortOrder', type: 'radio-group', version: '',
            options: [
                {value: 'ascend', label: ''},
                {value: 'descend', label: ''}],
            desc: '',
        },
        {
            label: '', field: 'fixed', type: 'radio-group', version: '',
            options: [
                {value: 'left', label: ''},
                {value: 'right', label: ''},
            ],
            desc: '',
        },
    ],
};


