const result = {
    ...Icons,
};
const req = require.context('./', true, /\.js$/);

req.keys().forEach(key => {
    if ([
        './options.js',
        './index.js',
        './icon.js',
    ].includes(key)) return;

    const model = req(key);
    const keys = key.split('/');
    let fileName = keys.pop().replace('.js', '');

    result[fileName] = model.default;
});

