import React from 'react';
import PropTypes from 'prop-types';
import {Icon} from 'src/components';
import {Tooltip} from 'antd';
import s from './style.less';

const quickPositionOptions = [
    {value: 'topLeft', icon: <Icon type="icon-layout-top-left"/>, label: ''},
    {value: 'top', icon: <Icon type="icon-layout-top"/>, label: ''},
    {value: 'topRight', icon: <Icon type="icon-layout-top-right"/>, label: ''},
    {value: 'left', icon: <Icon type="icon-layout-left"/>, label: ''},
    {value: 'center', placement: 'top', icon: <Icon type="icon-layout-center"/>, label: ''},
    {value: 'right', icon: <Icon type="icon-layout-right"/>, label: ''},
    {value: 'bottomLeft', icon: <Icon type="icon-layout-bottom-left"/>, label: ''},
    {value: 'bottom', icon: <Icon type="icon-layout-bottom"/>, label: ''},
    {value: 'bottomRight', icon: <Icon type="icon-layout-bottom-right"/>, label: ''},
];

function QuickPosition(props) {
    const {onClick, type, selectedKey} = props;
    const isLine = type === 'line';

    return (
        <div className={[s.root, s[type]]}>
            {quickPositionOptions.map(item => {
                const {value, placement, label, icon} = item;

                const sk = typeof selectedKey === 'function' ? selectedKey(item) : selectedKey;

                const isSelected = value === sk;

                return (
                    <Tooltip key={value} placement={isLine ? 'top' : placement || value} title={label}>
                        <div
                            className={s[isSelected]}
                            style={{cursor: 'pointer'}}
                            onClick={() => onClick(item)}
                        >
                            {icon}
                        </div>
                    </Tooltip>
                );
            })}
        </div>
    );
}

QuickPosition.propTypes = {
    type: PropTypes.string,
    onClick: PropTypes.func,
    selectedKey: PropTypes.any,
};

QuickPosition.defaultProps = {
    type: 'line',
    onClick: () => undefined,
};

export default QuickPosition;
