import {isMac} from 'src/drag-page/util';

export const colFields = [
    {label: '', field: 'flex', type: 'unit', placeholder: 'flex  1  100px', version: '', desc: 'flex '},
    {label: '', field: 'span', type: 'number', version: '', desc: ' 0  display: none'},
    {label: '', span: 12, field: 'offset', type: 'number', defaultValue: 0, version: '', desc: ''},
    {label: '', span: 12, labelWidth: '80px', field: 'order', type: 'number', defaultValue: 0, version: '', desc: ''},
    {label: '', span: 12, field: 'pull', type: 'number', defaultValue: 0, version: '', desc: ''},
    {label: '', span: 12, labelWidth: '80px', field: 'push', type: 'number', defaultValue: 0, version: '', desc: ''},
];

const screenType = (field) => [
    {value: 'number', label: ''},
    {
        value: 'object', label: '',
        fields: colFields.filter(item => item.field !== 'flex').map(item => {

            return {
                ...item,
                onKeyDown: getOnKeyDown(value => ({[field]: {[item.field]: value}})),
            };
        }),
    },
];

export function getOnKeyDown(cb, ancestorComponentName = 'Row') {

    return (e, options) => {
        const {metaKey, ctrlKey, key, target: {value}} = e;
        const isEnter = key === 'Enter';
        const {node, dragPageAction} = options;
        if ((metaKey || ctrlKey) && isEnter) {
            //  
            let nextValue = value;

            if (nextValue && !window.isNaN(nextValue)) {
                nextValue = window.parseFloat(nextValue);
            }

            const props = cb(nextValue);

            dragPageAction.syncOffspringProps({
                node,
                ancestorComponentName,
                props,
            });
        }
    };
}

export default {
    withHolder: true,
    holderProps: {style: {height: 54}},
    dropInTo: 'Row', // Col Row
    fields: [
        {
            label: '',
            field: 'span',
            type: 'number',
            version: '',
            desc: ' 0  display: none',
            placeholder: `span    ${isMac ? '' : 'ctrl'}+Enter `,

        },
        ...(colFields.filter(item => item.field !== 'span')),

        {label: ' < 576px', field: 'xs', type: screenType('xs'), version: '', desc: ' < 576px '},
        {label: '  576px', field: 'sm', type: screenType('sm'), version: '', desc: '  576px '},
        {label: '  768px', field: 'md', type: screenType('md'), version: '', desc: '  768px '},
        {label: '  992px', field: 'lg', type: screenType('lg'), version: '', desc: '  992px '},
        {label: '  1200px', field: 'xl', type: screenType('xl'), version: '', desc: '  1200px '},
        {label: '  1600px', field: 'xxl', type: screenType('xxl'), version: '', desc: '  1600px '},
    ].map(item => {
        return {
            ...item,
            onKeyDown: getOnKeyDown(value => ({[item.field]: value})),
        };
    }),
};
