export default {
    isContainer: false,
    draggable: true,
    isWrapper: true,
    hooks: {
        afterPropsChange: options => {
            const {node, dragPageAction} = options;
            if (node?.props?.visible === false) {
                setTimeout(() => {
                    Reflect.deleteProperty(node.props, 'visible');

                    dragPageAction.updateNode(node);
                });
            }
        },
    },
    fields: [
        {label: '', field: 'visible', type: 'boolean', version: '', desc: ''},
        {label: '', field: 'title', type: 'string', version: '', desc: ''},
        {label: '', field: 'content', type: 'ReactNode', version: '', desc: ''},
        {
            label: '', field: 'trigger', type: 'radio-group', defaultValue: 'hover',
            options: [
                {value: 'click', label: ''},
                {value: 'hover', label: ''},
                {value: 'focus', label: ''},
            ],
            version: '', desc: '',
        },
        {label: '', field: 'placement', type: 'placement', defaultValue: 'top'},
    ],
};
