import { getFieldUUID } from 'src/drag-page/util';

export default {
    isContainer: true,
    editableContents: [
        {
            selector: '.ant-drawer-title',
            propsField: 'title',
        },
    ],

    // draggable: false,
    componentDisplayName: ({node}) => {
        const {componentName, props = {}} = node;
        const {title} = props;

        if (!title) return componentName;

        return `${componentName} ${title}`;
    },

    propsToSet: {
        onClick: 'func.handleShowDrawer',
    },
    hooks: {
        beforeAdd: options => {
            const {node, dragPageState: {pageConfig}} = options;
            if (!node.props) node.props = {};
            const uid = getFieldUUID();
            const field = `visible__${uid}`;
            const handleDrawerClose = `handleDrawerClose__${uid}`;
            const handleDrawerShow = `handleDrawerShow__${uid}`;
            node.__config = {
                pageState: {
                    [field]: false,
                },
                pageFunction: {
                    [handleDrawerShow]: `() => {setState({${field}: true})}`,
                    [handleDrawerClose]: `() => {setState({${field}: false})}`,
                },
            };

            node.props.visible = `state.${field}`;
            node.props.onClose = `func.${handleDrawerClose}`;
            node.propsToSet = {
                onClick: `func.${handleDrawerShow}`,
            };

            if (!pageConfig.children) pageConfig.children = [];
            pageConfig.children.push(node);
        },
    },
    fields: [
        {label: '', category: '', field: 'closable', type: 'boolean', defaultValue: true, version: '', desc: ''},
        {label: 'esc ', category: '', field: 'keyboard', type: 'boolean', defaultValue: true, version: '', desc: ' esc '},
        {label: '', category: '', field: 'mask', type: 'boolean', defaultValue: true, version: '', desc: ''},
        {label: '', appendField: {closable: true}, field: 'closeIcon', type: 'ReactNode', version: '', desc: ''},
        {
            label: '', field: 'title',
            type: [
                {value: 'string', label: ''},
                {value: 'ReactNode', label: ''},
            ],
            version: '', desc: '',
        },
        {
            label: '',
            field: 'placement',
            type: 'radio-group',
            defaultValue: 'right',
            version: '',
            options: [
                {value: 'top', label: ''},
                {value: 'right', label: ''},
                {value: 'bottom', label: ''},
                {value: 'left', label: ''},
            ],
            desc: '',
        },
        {label: '', field: 'footer', type: 'ReactNode', version: '', desc: ''},
        {label: '', appendField: {placement: ['top', 'bottom']}, field: 'height', type: 'unit', defaultValue: 256, version: '', desc: ',  placement  top  bottom '},
        {label: '', field: 'width', type: 'unit', defaultValue: '256', version: '', desc: ''},
        {label: 'z-index', field: 'zIndex', type: 'number', defaultValue: '1000', version: '', desc: ' Drawer  z-index'},
    ],
};

