import {Button, Modal} from 'antd';
import {QuestionCircleOutlined} from '@ant-design/icons';
import ReactMarkdown from 'react-markdown';
import {isMac} from 'src/drag-page/util';
import React from 'react';

const markdown = `
## 

1. ctrl + f 
1. ctrl + click 
1. space +  

1. 
    1. ctrl + d 
    1. ctrl + c 
    1. ctrl + v ****

1. 
    1. ctrl 
    1. alt 
    1. shift 

`.replaceAll('ctrl', isMac ? '' : 'ctrl');


export default function Help(props) {
    const {onCancel, visible} = props;
    return (
        <Modal
            visible={visible}
            width={800}
            style={{top: 42}}
            title={<><QuestionCircleOutlined/> </>}
            onCancel={onCancel}
            footer={<Button type="primary" onClick={onCancel}></Button>}
        >
            <ReactMarkdown>
                {markdown}
            </ReactMarkdown>
        </Modal>
    );
}
